// ========================================
// Nav Bar
// ========================================

.logo-container		{
	display: flex;
	justify-content: center;
	width: 100%;
	@include b-pri(bottom);
}

.logo				{
	font-size: 5rem;
	font-weight: 900;
	color: $color-pri;
}

.links				{
	text-align: center;
	padding: 5rem 0 1rem;
	>:last-child		{
		@include b-sec(top,bottom);
	}
	li 					{
		margin: 1rem 0;
	}
	li:nth-child(-n+4)>a	{
		position: relative;
		@include fancy-line;
	}
	a 					{
		font-size: .925rem;
		font-weight: 400;
		line-height: 2rem;
		@include fancy-text(1rem);
	}
}



.social				{
	text-align: center;
	a 					{
		color: $color-pri;
		font-size: 2rem;
		padding: 0 0.25rem;
	}
}